body { padding-top: 70px; }

.menu-icon {
    font-size: 1.25rem;
}
.current {
  opacity: 0.8;
  color: white;
}
.popup {
  padding-top: 10px;
  padding-bottom: 20px;
  padding-left: 20px;
  background-color: #fff;
  -webkit-border-radius: 6px 6px 6px 6px;
  -moz-border-radius: 6px 6px 6px 6px;
  border-radius: 6px 6px 6px 6px;
  -webkit-box-shadow: 1px 5px 2px rgba(0, 0, 0, .15);
  -moz-box-shadow: 1px 5px 2px rgba(0, 0, 0, .15);
  box-shadow: 1px 5px 2px rgba(0, 0, 0, .15);
}

.box {
  background-color: #FDFDFD;
  padding-top: 10px;
  padding-left: 10px;
  -webkit-border-radius: 6px 6px 6px 6px;
  -moz-border-radius: 6px 6px 6px 6px;
  border-radius: 6px 6px 6px 6px;
  -webkit-box-shadow: inset 0 0px 5px rgba(0, 0, 0, .15);
  -moz-box-shadow: inset 0 0px 5px rgba(0, 0, 0, .15);
  box-shadow: inset 0 0px 5px rgba(0, 0, 0, .15);
}

img { margin-bottom: 24px; 
  margin-top: 17px; 
  border-radius:2%;
  max-width: 100%;
}


/* Home, News */
#newsid p {
text-align: left;
}

#homeidtmp h1 {
text-align: center;
}

#newsid img {
margin-top: 6px;
margin-bottom: 6px;
}

/* Team & Publications */
#gridid img { margin: 16px 22px 6px 0; border-radius:10%; box-shadow: 2px 2px 5px #888888 }
#homeid img { margin: 0 0 0 0; border-radius:5%; box-shadow: 0px 0px 0px #888888 }
#picid img { margin: 6px 3px 60px 6px; border-radius:1%; box-shadow: 2px 2px 5px #888888 }
#gridid .row { margin-bottom: 24px; }


/* Grid overrides */
.col-sm-1, .col-sm-2, .col-sm-3, .col-sm-5, .col-sm-6,
.col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12 {
padding-left: 16px;
padding-right: 16px;
}

/* Grid overrides */
.col-sm-4 {
padding-left: 26px;
padding-right: 26px;
}


/* Carusel */

.carousel {
 border-radius: 3px 3px 3px 3px;
 margin-bottom: 20px;
 margin-top: 20px;
 margin-left: 30px;
 margin-right: 30px;
}